/* media queries */
@media only screen and (max-width: 1200px) {
  .top nav {
    display: none;
  }

  .top .buttons {
    display: none;
  }

  .top .menu {
    visibility: visible;
    background-color: #37b12c;
    border: 0px;
    border-radius: 7px;
    cursor: pointer;
  }
  /* new menu */
  .banner .new-menu {
    position: absolute;
    background-color: #4d4d4d;
    left: 0;
    top: 0;
    right: 0;
    z-index: 8;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }

  .banner .new-nav {
    padding-left: 100px;
    margin: 0px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  .banner .new-nav li {
    border-bottom: 1px solid #dbd8d5;
    margin: 0px;
    list-style: none;
  }

  .new-nav li a:hover {
    background-color: #f1eded;
    font-weight: bold;
  }

  .new-nav li a {
    display: block;
    font-size: 1.2em;
    padding: 8px 0px;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: 100;
    width: 100%;
    height: 100%;
    transition: background-color 500ms ease-in-out;
  }

  .banner .menu {
    background-color: transparent;
    border: 0px;
    border-radius: 7px;
    cursor: pointer;
  }
}

